import md5 from 'js-md5';
import {Message} from 'element-ui'
// 配置API接口地址
// var root = 'http://10.108.12.57:28060/api/'
var root = 'http://crm.ajhroro.com/api/'
// var root = 'http://crm.ajhroro.com/apitest/'

// 引用axios
import axios from 'axios'
// var axios = require('axios')

// 自定义判断元素类型JS
function toType(obj) {
  return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}

// 参数过滤函数
function filterNull(o) {
  for (var key in o) {
    if (o[key] === null) {
      delete o[key]
    }
    if (toType(o[key]) === 'string') {
      o[key] = o[key].trim()
    }
    else if (toType(o[key]) === 'object') {
      o[key] = filterNull(o[key])
    }
    else if (toType(o[key]) === 'array') {
      o[key] = filterNull(o[key])
    }
  }
  var d = sign(o);
  return d
}

/* token校验，加密校验 */
function sign(p) {
  let token;
  let user = sGetObject("_user");
  let time = new Date().getTime();
  if (user == undefined || user == null) {
    token = '';
  }
  else {
    token = user.token;
  }

  var arr = [];
  for (var item in p) {
    arr.push(item);
  }

  var paramsObj = {};
  var operate = {
    operateCode: '',
    operateName: '',
  };
  for (var i = 0; i < arr.length; i++) {
    var key = arr[i];
    var value = p[key];
    if(key == 'operateCode' || key == 'operateName'){
      operate.operateCode = p['operateCode'];
      operate.operateName = p['operateName'];
    }
    else{
      paramsObj[key] = value;
    }
  }
  // 签名校验
  var cr = {
    token: token,
    time: time,
    reqData: JSON.stringify(paramsObj)
  };
  // 排序key
  var keyArr = [];
  for (var item in cr) {
    if (item != "sign") {
      keyArr.push(item);
    }
  }
  keyArr.sort();
  // 生成加密
  var content = "";
  for (var i = 0; i < keyArr.length; i++) {
    var key = keyArr[i];
    var value = cr[key];
    content += key + value;
  }
  var signData = {
    form: 3,
    operateCode: operate.operateCode,
    operateName: operate.operateName,
    token: token,
    time: time,
    reqData: paramsObj,
    sign: md5(content).toUpperCase()
  }
  // console.log(signData);
  // return;
  return signData;
};

/*
  接口处理函数
  这个函数每个项目都是不一样的，我现在调整的是适用于
  https://cnodejs.org/api/v1 的接口，如果是其他接口
  需要根据接口的参数进行调整。参考说明文档地址：
  https://cnodejs.org/topic/5378720ed6e2d16149fa16bd
  主要是，不同的接口的成功标识和失败提示是不一致的。
  另外，不同的项目的处理方法也是不一致的，这里出错就是简单的alert
*/

function apiAxios(method, url, params, success, failure) {
  if (params) {
    params = sign(params);
  }
  // if (params) {
  //   params = filterNull(params)
  // }
  axios({
    method: method,
    url: url,
    data: method === 'POST' || method === 'PUT' ? params : null,
    params: method === 'GET' || method === 'DELETE' ? params : null,
    baseURL: root,
    withCredentials: false,
    timeout: 5000
  })
    .then(function (res) {
      if (res.data.repCode === '0000') {
        if (res.data.success === true) {
          if (success) {
            success(res.data)
          }
        } else {
          if (failure) {
            failure(res.data)
          } else {
            Message.error({
              message: JSON.stringify(res.data)
            })
            // window.alert('error: ' + JSON.stringify(res.data))
          }
        }
      }
      else if (res.data.repCode === '0104') {
        Message.error({
          message: '登录过期',
          duration: 1000,
          onClose: function () {
            sessionStorage.clear();
            window.location.reload();
          }
        })
      }
      else {
        Message.error({
          message: JSON.stringify(res.data.repMsg)
        })
      }
    })
    .catch(function (err) {
      console.log(err);
      Message.error({
        message: '服务器异常'
      })
      // let res = err.response
      // if (err) {
      //   Message.error({
      //     message: res.status
      //   })
      //   window.alert('api error, HTTP CODE: ' + res.status)
      // }
    })
}

function sGetObject(k) {
  try {
    var v = sessionStorage.getItem(k);
    return v == null ? null : JSON.parse(v);
  } catch (e) {
  }
};

// 返回在vue模板中的调用接口
export default {
  get: function (url, params, success, failure) {
    return apiAxios('GET', url, params, success, failure)
  },
  post: function (url, params, success, failure) {
    return apiAxios('POST', url, params, success, failure)
  },
  put: function (url, params, success, failure) {
    return apiAxios('PUT', url, params, success, failure)
  },
  delete: function (url, params, success, failure) {
    return apiAxios('DELETE', url, params, success, failure)
  },
  sset: function(k, v) {
    try {
      sessionStorage.setItem(k, v);
    } catch(e) {

    }
  },
  sget: function(k) {
    try {
      return sessionStorage.getItem(k);
    } catch(e) {
      return null;
    }
  },
  sSetObject: function (k, v) {
    try {
      sessionStorage.setItem(k, JSON.stringify(v));
    } catch (e) {

    }
  },
  sGetObject: function (k) {
    try {
      var v = sessionStorage.getItem(k);
      return v == null ? null : JSON.parse(v);
    } catch (e) {

    }
  },
  sdel: function(k) {
    sessionStorage.removeItem(k);
  },
  sclear: function() {
    sessionStorage.clear();
  }
}



